.footer {
  margin-top: 110px;
  background-color: #1d1e1e;
  padding-bottom: 40px;
  padding-top: 53px;

  &__logo {
    .logo {
      &__link {
        color: #6d6d6d;
      }
    }
  }

  &__text {
    color: #ffffff;
    font-family: "Panton-SemiBold";
    font-size: 13px;
    font-weight: 400;
    line-height: 24px;
    letter-spacing: 1.06px;
    margin-bottom: 55px;
  }

  &__copyright {
    color: #ffffff;
    font-family: "Panton-SemiBold";
    font-size: 11px;
    font-weight: 400;
    letter-spacing: 0.17px;
  }

  &__row {
    display: flex;

    &--jc {
      align-items: center;
      justify-content: space-between;
      border-top: 1px solid #ffffff;
      border-color: rgba(#ffffff, 0.4);
      padding-top: 46px;
      margin-top: 63px;
    }
  }

  &__link {
    color: #009be1;
    font-size: 14px;
    font-weight: 700;
    text-decoration: underline;
    letter-spacing: 0.14px;
    display: flex;

    margin-bottom: 15px;

    &:last-child {
      margin-bottom: 0;
    }
  }

  &__column {
    &:first-child {
      margin-right: 123px;
    }

    &:last-child {
      display: flex;
      flex: 1;
      justify-content: space-between;
      margin-top: 9px;
    }
  }

  &__subcontent {
    margin-left: 20px;

    &:first-child {
      margin-left: 0;
    }
  }

  &__subtitle {
    color: #ffffff;
    font-size: 12px;
    font-weight: 700;
    text-transform: uppercase;
    letter-spacing: 2.4px;
    margin-bottom: 19px
  }

  &__subitem {
    margin-bottom: 10px;

    &:last-child {
      margin-bottom: 0;
    }
  }

  &__sublink {
    color: #ffffff;
    font-size: 12px;
    font-weight: 700;
    text-transform: uppercase;
    letter-spacing: 0.6px;
    opacity: 0.6;
  }
}
