.options-list {
  &__bar {
    display: flex;
    align-items: center;
  }

  &__item {
    margin-right: 70px;

    &:last-child {
      margin-right: 0;
    }
  }

  &__link {
    display: inline-block;
    position: relative;
  }

  &__counter {
    color: #009ee2;
    font-family: "Panton-SemiBold";
    font-size: 16px;
    font-weight: 400;
    letter-spacing: 0.4px;

    position: absolute;
    top: -7px;
    right: -13px;
  }
}
