.navigation {
  background-color: #efefef;
  margin-bottom: 31px;

  &__list {
    display: flex;
    align-items: center;
  }

  &__item {
    margin-right: 83px;
    position: relative;

    &:last-child {
      margin-right: 0;
    }

    &:hover{
      &:before{
        border-top-color: #fff;
      }
    }

    &:before{
      content:'';
      display: inline-block;
      border:14px solid transparent;
      position: absolute;
      left: 50%;
      transform: translateX(-50%);
      top: -3px;
      transition: .4s;
    }

    &--main {
      margin-right: 41px;

      &:before{
        display: none;
      }

      .navigation__link {
        color: #fff;
        background-image: linear-gradient(to left, #0187c1 0%, #3ac0ef 100%);
        padding-left: 17px;
        padding-right: 23px;
        display: inline-flex;
        flex-wrap: nowrap;

        &:hover {
          color: #fff;
        }

        &:before,
        &:after {
          content: "";
          display: inline-block;
        }

        &:before {
          width: 25px;
          height: 18px;
          background: url(../../img/menu.png) center no-repeat;
          margin-right: 20px;
        }

        &:after {
          width: 6px;
          height: 11px;
          background: url(../../img/arrow-grey.png) center no-repeat;
          background-size: contain;
          transform: rotate(90deg);
          margin-left: 39px;
        }
      }
    }
  }

  &__link {
    display: inline-block;
    color: #1d1e1e;
    font-size: 14px;
    font-weight: 700;
    text-transform: uppercase;
    letter-spacing: 0.7px;
    transition: color 0.4s;
    padding: 20px 0;
    white-space: nowrap;

    &:hover {
      color: #009ee2;
    }

    
  }
}
