.distribution {
  &__text {
    color: #1d1e1e;
    font-size: 24px;
    font-weight: 700;
    line-height: 36px;
    margin-bottom: 30px;
    text-align: center;
  }

  &__row {
    display: flex;
  }

  &__input {
    width: 100%;
    .input {
      width: 100%;
      padding: 18px 30px;
      border-radius: 4px 0 0 4px;

      font-family: "Panton-SemiBold";
      font-size: 18px;
      font-weight: 400;
    }
  }

  &__button {
    .button {
      padding: 17px 50px;
    }
  }
}
