.bar-option {
  display: flex;
  align-items: center;

  &__link {
    display: inline-flex;
    width: 25px;
    height: 25px;
    background-color: #1d1e1e;
    border-radius: 50%;
    margin-right: 5px;
    position: relative;
    transition: opacity 0.3s;

    &:last-child {
      margin-right: 0;
    }

    &:hover {
      opacity: 0.7;
    }

    &:before {
      content: "";
      display: inline-block;
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      width: 6px;
      height: 10px;
      background: url(../../img/arrow.png) center no-repeat;
    }

    &--next {
      &:before {
        transform: translate(-50%, -50%) rotate(180deg);
      }
    }
  }
}
