.stars {
  &__list {
    display: flex;
    align-items: center;
  }

  &__item {
    margin-right: 3px;

    &:last-child {
      margin-right: 0;
    }
  }

  &__link{
      display: inline-block;

      &:before{
        content:'';
        display: inline-block;
        background: url(../../img/star.png) center no-repeat;
        height: 15px;
        width: 15px;
      }
  }
}
