.sidebar {
  width: 260px;
  max-width: 100%;
  position: relative;

  &:after {
    content: "";
    display: block;
    position: absolute;
    top: 96%;
    height: 71px;
    width: 100%;
    background: url(../../img/shadow.png) center no-repeat;
    z-index: -1;
  }

  &__item {
    background-color: #efefef;
    border-bottom: 1px solid #fff;
    transition: opacity 0.3s;

    &:hover {
      opacity: 0.7;
    }

    &:last-child {
      border-bottom: none;
    }
  }

  &__link {
    display: flex;
    align-items: center;
    color: #1d1e1e;
    font-size: 15px;
    font-weight: 700;
    letter-spacing: 0.75px;
    transition: opacity 0.3s;
    padding: 14px 22px;
    padding-right: 14px;

    &:hover {
      opacity: 0.7;
    }

    &:before,
    &:after {
      content: "";
      display: inline-block;
    }

    &:before {
      width: 5px;
      height: 5px;
      border: 3px solid #1d1e1e;
      border-radius: 50%;
      margin-right: 25px;
    }

    &:after {
      width: 6px;
      height: 11px;
      background: url(../../img/arrow.png) center no-repeat;
      transform: rotate(180deg);
      margin-left: auto;
    }
  }
}
