.last-news {
  margin-top: 99px;
  border-top: 1px solid #efefef;
  padding-top: 42px;

  &__list {
    display: flex;
  }

  &__item {
    width: calc(100% / 3);
    margin-right: 30px;
    position: relative;
    box-shadow: 0 18px 21px rgba(0, 0, 0, 0.09);
    padding-bottom: 17px;

    &:last-child {
      margin-right: 0;
    }
  }

  &__button {
    .button {
      width: initial;
    }
  }

  &__distribution{
    margin-top: 47px;
  }

  .content-list {

    &__bar{
      margin-bottom: 56px;
    }

    &__item {
      &-picture {
        padding: 0;
        margin-bottom: 14px;
      }

      &-info {
        padding: 0 23px;
      }

      &-text {
        color: #1d1e1e;
        font-family: "Panton-SemiBold";
        font-size: 13px;
        font-weight: 400;
        line-height: 22px;
        letter-spacing: 0.33px;
        margin-bottom: 18px;
      }

      &-category {
        color: #1d1e1e;
        font-family: "Panton-ExtraBold";
        text-transform: uppercase;
        margin-bottom: 14px;
      }
    }
  }
}
