.socials {
  &__list {
    display: flex;
    align-items: center;
  }

  &__item {
    margin-right: 55px;

    &:last-child {
      margin-right: 0;
    }
  }

  &__link {
    display: inline-block;

    &:before {
      content: "";
      display: inline-block;
      background-repeat: no-repeat;
      background-position: center;
    }

    &--vk {
      &:before {
        width: 23px;
        height: 14px;
        background: url(../../img/vk.png);
      }
    }

    &--fb {
      &:before {
        width: 11px;
        height: 19px;
        background: url(../../img/facebook.png);
      }
    }

    &--inst {
      &:before {
        width: 19px;
        height: 19px;
        background: url(../../img/instagram.png);
      }
    }
  }
}
