.post-main {
  display: flex;

  &__content {
    flex: 1;
    margin-left: 30px;
  }

  &__list {
    display: flex;
    flex-wrap: wrap;

    &-indicator {
      position: absolute;
      bottom: 34px;
      left: 43px;
      display: flex;
      align-items: center;

      &-item {
        margin-right: 20px;

        &:last-child {
          margin-right: 0;
        }

        &--active {
          .post-main__list-indicator-link {
            border-color: #3dc3f2;
          }
        }
      }

      &-link {
        display: inline-block;
        width: 11px;
        height: 11px;
        border: 3px solid #fff;
        border-radius: 50%;

        &:hover {
          transition: 0.3s;
        }
      }
    }
  }

  &__item {
    border-radius: 4px;
    background: #048bc4;
    display: flex;
    width: calc(100% / 3);
    margin-right: 30px;
    padding-top: 20px;
    padding-left: 22px;
    width: 100%;
    max-width: 262px;
    max-height: 172px;
    box-shadow: 0 18px 21px rgba(0,0,0,.09);

    &--main {
      width: 100%;
      min-width: 100%;
      max-height: initial;
      padding-top: 59px;
      padding-left: 42px;
      position: relative;
      margin-bottom: 27px;

      .post-main__item-title {
        font-family: "Panton-ExtraBold";
        font-size: 40px;
        font-weight: 400;
        line-height: 50px;
        letter-spacing: 2px;
        margin-bottom: 17px;
      }

      .post-main__item-info {
        margin-right: 65px;
      }

      .post-main__item-pic {
        margin-top: -76px;
      }

      .post-main__item-desc {
        color: #4bd1fe;
        font-size: 22px;
        line-height: 30px;
        letter-spacing: 1.1px;
        margin-bottom: 1px;
      }

      .post-main__item-button {
        .button {
          background-color: #1d1e1e;
          color: #ffffff;
          font-size: 16px;
          font-weight: 700;
          line-height: 25px;
          text-transform: uppercase;
          letter-spacing: 3.2px;
          width: initial;
          padding: 15px 46px;
        }
      }

      .post-main__item-price {
        margin-bottom: 22px;

        .price {
          &__value,
          &__currency {
            color: #ffffff;
            font-family: "Panton";
            font-size: 30px;
            font-weight: 400;
            line-height: 45px;
          }
        }
      }
    }

    &:last-child {
      margin-right: 0;
    }

    &-title {
      color: #ffffff;
      font-size: 20px;
      font-weight: 700;
      line-height: 26px;
      text-transform: uppercase;
      letter-spacing: 0.5px;
      margin-bottom: 8px;
    }

    &-pic{
        &-icon{
            height: 100%;
        }
    }

    &-desc {
      color: #4cd2fe;
      font-family: "Panton-SemiBold";
      font-size: 14px;
      font-weight: 400;
      letter-spacing: 0.7px;
      margin-bottom: 12px;
    }
  }
}
