.about {
  margin-top: 99px;

  &__text {
    color: #1d1e1e;
    font-family: "Panton-SemiBold";
    font-size: 14px;
    font-weight: 400;
    line-height: 25px;
    letter-spacing: 0.35px;
  }

  &__wrapp {
    display: flex;
    margin-top: 43px;
  }

  &__col {
    flex: 1;
    margin-right: 42px;

    &:last-child {
      margin-right: 0;
    }
  }

  &__preposition {

    &-list {
      display: flex;
    }

    &-row {
      display: flex;

      &--al {
        align-items: center;
        margin-left: 6px;
      }
    }

    &-wrapp {
      padding-top: 44px;
      padding-left: 39px;
    }

    &-item {
      display: flex;
      border-radius: 4px;
      width: calc(100% / 2);
      margin-right: 30px;
      background-image: linear-gradient(to right, #0187c1 0, #3ac0ef 100%);
      box-shadow: 0 18px 21px rgba(0,0,0,.09);

      &:last-child {
        margin-right: 0;
      }

      &-title {
        color: #ffffff;
        font-family: "Panton-ExtraBold";
        font-size: 48px;
        font-weight: 400;
        line-height: 52px;
        letter-spacing: 1.2px;
        white-space: nowrap;
      }

      &-date {
        color: #5dd5fe;
        font-family: "Panton-SemiBold";
        font-size: 22px;
        font-weight: 400;
        letter-spacing: 0.55px;
        white-space: nowrap;
        margin-top: 12px;
        margin-left: -98px;
      }

      &-discount {
        opacity: 0.22;
        color: #ffffff;
        font-family: "Panton-ExtraBold";
        font-size: 95px;
        font-weight: 400;
        line-height: 130.14px;
        margin-left: 54px;
      }

      &-bar-option {
        margin-top: 17px;

        .bar-option {
          &__link {
            &--next {
              width: 37px;
              height: 37px;
            }
          }
        }
      }

      &-pic{
        margin-left: -19px;
        margin-top: -10px;
        z-index: 1;
      }
    }
  }

  &__content-list{
    margin-top: 88px;
  }

}

