.menu {
  &__list {
    display: flex;
    align-items: center;
  }

  &__item {
    margin-right: 28px;

    &:after {
      content: "";
      display: inline-block;
      width: 4px;
      height: 4px;
      border: 2px solid #6a6a6a;
      border-radius: 50%;
      margin-left: 27px;
    }

    &:last-child {
      margin-left: 0;

      &:after{
        display: none;
      }
    }
  }

  &__link {
    color: #ffffff;
    font-size: 14px;
    font-weight: 400;
    letter-spacing: 0.35px;
  }
}
