.label {
  position: absolute;
  display: inline-block;
  padding: 10px 15px;
  border-radius: 2px;
  left: 0;

  &__text {
    color: #ffffff;
    font-family: "Panton-ExtraBold";
    font-size: 12px;
    font-weight: 400;
    text-transform: uppercase;
    letter-spacing: 1.45px;
  }

  &--hit {
    background-image: linear-gradient(to left, #0187c1 0%, #3ac0ef 100%);
    top: 39px;
  }

  &--discount{
    background-color: #00963f;
    top: 0;
  }

  &--date{
    top: 0;
    background-color: #1d1e1e;
  }
}
