@font-face {
  font-family: "Panton";
  src: url("../fonts/Panton.woff") format("woff");
  font-weight: 400;
}

@font-face {
  font-family: "Panton-Black";
  src: url("../fonts/Panton-Black.woff") format("woff");
}

@font-face {
  font-family: "Panton-SemiBold";
  src: url("../fonts/Panton-SemiBold.woff") format("woff");
}

@font-face {
  font-family: "Panton-ExtraBold";
  src: url("../fonts/Panton-ExtraBold.woff") format("woff");
}

*{
  box-sizing: border-box;
}

body,
html {
  margin: 0;
  padding: 0;
  height: 100%;
  width: 100%;
  font-family: "Panton";
}

ul,
li {
  list-style: none;
  margin: 0;
  padding: 0;
}

a {
  text-decoration: none;
}

p {
  margin: 0;
  padding: 0;
}

input {
  outline: none;
  border: 1px solid transparent;
}

button {
  outline: none;
  border: 1px solid transparent;
  cursor: pointer;
}

.wrapper {
  height: 100%;
  width: 100%;
  overflow-x: hidden;
}

.container {
  width: 1140px;
  max-width: 100%;
  margin: 0 auto;
  height: 100%;
}

.title {
  color: #1d1e1e;
  font-family: "Panton-ExtraBold";
  font-size: 36px;
  font-weight: 400;
  text-transform: uppercase;
  letter-spacing: 1.8px;
}
.button {
  background-color: #009ee2;
  color: #ffffff;
  font-size: 16px;
  font-weight: 700;
  line-height: 22px;
  text-transform: uppercase;
  letter-spacing: 3.2px;
  width: 100%;
  padding: 15px 0;
  border-radius: 4px;
  transition: opacity .4s;

  &:hover{
    opacity: .7;
  }

  &--news {
    color: #0295d5;
    font-family: Panton;
    font-size: 10px;
    font-weight: 700;
    text-decoration: underline;
    text-transform: uppercase;
    /* Text style for "п, одробне" */
    letter-spacing: 2px;
    background: transparent;
    padding: 0;
  }
}

.input {
  border: 1px solid #dddddd;
  color: #1d1e1e;
  font-size: 14px;
  font-weight: 400;
  letter-spacing: 0.7px;
  width: 505px;
  max-width: 100%;
  padding: 12px 25px;
}
