.price {
  display: flex;
  align-items: center;

  &__value,
  &__currency {
    color: #000000;
    font-family: "Panton-ExtraBold";
    font-size: 18px;
    font-weight: 400;
    letter-spacing: 0.45px;
  }

  &__value {
    margin-right: 11px;
  }

  &__old {
    display: flex;
    align-items: center;
    margin-left: 15px;
    overflow: hidden;
    position: relative;

    &:before{
      content:'';
      display: block;
      position: absolute;
      top: 50%;
      transform: translateY(-50%);
      width: 100%;
      background-color: #6d6d6d;
      height: 1px;
    }

    .price__value , .price__currency {
      color: #6d6d6d;
      font-family: "Panton-SemiBold";
      font-size: 14px;
    }

    .price__value{
      margin-right: 4px;
    }
  }
}
