.content-list {
  &__bar {
    display: flex;
    margin-bottom: 40px;

    &-wrapp {
      display: flex;
    }

    &-option {
      margin-left: auto;
    }
  }

  &__title {
    margin-right: 31px;
  }

  &__subtitle {
    color: #009ee2;
    font-size: 11px;
    font-weight: 700;
    text-transform: uppercase;
    letter-spacing: 2.2px;
    align-self: flex-end;
    margin-bottom: 8px;
    text-decoration: underline;
  }

  &__options {
    display: flex;
  }

  &__item {
    width: calc(100% / 4);
    margin-right: 27px;
    box-shadow: 0 18px 21px rgba(0, 0, 0, 0.09);
    border: 1px solid #efefef;
    position: relative;
    padding-bottom: 15px;
    border-radius: 5px;

    &--other {
      background: #048bc4;
      padding-top: 18px;
      padding-left: 10px;

      .content-list__item-category {
        color: #ffffff;
        font-family: "Panton-ExtraBold";
        font-size: 33px;
        font-weight: 400;
        letter-spacing: 0.82px;
        margin-bottom: 8px;
      }

      .content-list__item-name {
        color: #4cd2fe;
        font-family: "Panton-SemiBold";
        font-size: 22px;
        font-weight: 400;
        line-height: 30px;
        letter-spacing: 0.55px;
        margin-bottom: 0;
      }

      .content-list__item-picture{
        padding: 0;
        margin-top: -47px;
        margin-left: -3px;
        margin-bottom: -2px;

        &-icon{
          width: initial;
        }
      }

      .content-list__item-price {
        .price {
          &__value,
          &__currency {
            color: #fff;
            font-family: "Panton";
            font-size: 30px;
            font-weight: 400;
            line-height: 45px;
          }
        }
      }
    }

    &:last-child {
      margin-right: 0;
    }

    &-picture {
      max-width: 100%;
      padding: 25px 13px;
      margin-bottom: 5px;

      &-icon {
        height: 100%;
        width: 100%;
        object-fit: cover;
      }
    }

    &-info {
      padding: 0 16px;
    }

    &-row {
      display: flex;
      align-items: center;
      justify-content: space-between;
      margin-bottom: 7px;
    }

    &-like {
      margin-right: 9px;
    }

    &-category,
    &-name {
      color: #000000;
      font-family: "Panton-SemiBold";
      font-size: 16px;
      font-weight: 400;
      letter-spacing: 0.4px;
    }

    &-name {
      margin-bottom: 10px;
    }

    &-button {
      margin-top: 10px;
    }

    &-price-benefit {
      color: #00963f;
      font-family: "Panton-SemiBold";
      font-size: 12px;
      font-weight: 400;
      letter-spacing: 0.3px;
    }
  }
}
